"use client";
import { useState } from "react";
import Script from "next/script";
import { Facebook } from 'lucide-react';
import { FaLine } from "react-icons/fa6";

export default function Contact() {
    const [form, setForm] = useState({
        name: "",
        email: "",
        phone: "",
        subject: "",
        message: ""
    });

    const [status, setStatus] = useState("");

    const handleChange = (e) => {
        setForm({ ...form, [e.target.name]: e.target.value});
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setStatus("...");
        try{
            const res = await fetch("/api/contacts", {
                method: "POST",
                headers: { "Content-Type": "application/json"},
                body: JSON.stringify(form),
            });

            if(!res.ok) throw new Error("Network Error");
            setStatus(" ");
            setForm({ name: "", email: "", phone: "", subject: "", message: "" });
        } catch (err){
            setStatus("  ");
        }
    }

    

    // Json-LD schema
    const jsonLd = {
        "@context": "https://schema.org",
        "@type": "Organization",
        "name": " ",
        "url": "https://morita-miyata.vercel-v2.app/",
        "logo": "https://morita-miyata-v2.vercel.app/group-brand-logo.webp?v=4",
        "contactPoint": {
            "@type": "ContactPoint",
            "telephone": "+66 081-911-6293",
            "contactType": "customer service",
            "areaServed": "TH",
            "availableLanguage": "Thai"
        }
    };
    return (
        <section className="py-20 bg-gradient-to-r from-red-700 to-red-700 text-white">
            {/* JSON-LD Schema  SEO */}
            <script
                id="json-ld-contact"
                type="application/ld+json"
                dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd)}}
                strategy="afterInteractive"
            />
            <div className="max-w-3xl mx-auto text-center mb-12 px-4">
                <h1 className="text-5xl font-bold mb-4"></h1>
                <p className="text-lg mb-6">
                       
                </p>
            </div>
            {/* Form Contact */}
            <form onSubmit={handleSubmit} className="max-w-3xl mx-auto bg-white rounded-2xl p-8 text-black shadow-lg space-y-4">
                <label htmlFor="name" className="sr-only"> - </label>
                <input
                    id="name"
                    type="text"
                    name="name"
                    placeholder=" - "
                    value={form.name}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg"
                    autoComplete="name"
                    required
                />
                <label htmlFor="email" className="sr-only"></label>
                <input
                    id="email"
                    type="email"
                    name="email"
                    placeholder=""
                    value={form.email}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg"
                    autoComplete="email"
                    required
                />
                <label htmlFor="phone" className="sr-only"></label>
                <input
                    id="phone"
                    type="tel"
                    name="phone"
                    placeholder=" ()"
                    value={form.phone}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg"
                    autoComplete="phone"
                />
                <label htmlFor="subject" className="sr-only"></label>
                <select
                    id="subject"
                    name="subject"
                    value={form.subject}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg"
                    required
                >
                    <option value="">--  --</option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
                <label htmlFor="message" className="sr-only"></label>
                <textarea
                    id="message"
                    name="message"
                    placeholder=""
                    value={form.message}
                    onChange={handleChange}
                    className="w-full p-3 border rounded-lg h-32"
                    required
                />
                <button type="submit" className="w-full py-3 bg-red-600 text-white rounded-lg hover:bg-white hover:outline-2 hover:outline-red-600 hover:text-black font-semibold transition duration-200 cursor-pointer">
                    
                </button>
                {status && <p className="text-center mt-2 text-sm">{status}</p>}
                <h2 className="text-lg font-bold text-center mt-2"></h2>
                {/* Other Social Media */}
                <div className="flex justify-center items-center gap-3 mt-4">
                    <a href="https://www.facebook.com/share/1Fm9rEEWyX/?mibextid=wwXIfr" target='_blank' rel="noopener noreferrer" 
                        className='w-10 h-10 flex items-center justify-center rounded-full text-black hover:text-white bg-white hover:bg-black transition'>
                        <Facebook className='w-7 h-7' />
                    </a>
                    <a href="https://line.me/R/ti/p/~titiratu" target='_blank' rel="noopener noreferrer" 
                        className='w-10 h-10 flex items-center justify-center rounded-full text-black hover:text-white bg-white hover:bg-black transition'>
                        <FaLine className='w-7 h-7' />
                    </a>
                </div>
                <p className="text-md text-gray-500 mt-2 text-center">
                
                
                /
                
                </p>
            </form>

            {/* Google Maps */}
            <div className="max-w-3xl mx-auto mt-12 px-4">
                <h2 className="text-2xl font-bold mb-4 text-center"></h2>
                <div className="w-full h-80 rounded-lg overflow-hidden shadow-lg">
                    <iframe 
                        src="https://www.google.com/maps/embed?pb=!1m17!1m12!1m3!1d483.4841934199448!2d100.59529691339449!3d14.202181665733685!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m2!1m1!2zMTTCsDEyJzA4LjEiTiAxMDDCsDM1JzQzLjciRQ!5e0!3m2!1sth!2sth!4v1756215242246!5m2!1sth!2sth"
                        width="100%"
                        height="100%"
                        style={{ border: 0 }}
                        allowFullScreen=""
                        loading="lazy"
                        referrerPolicy="no-referrer-when-downgrade"
                    >
                    </iframe>
                </div>
            </div>
        </section>
    )
}