"use client";
import { FaFireExtinguisher } from "react-icons/fa";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Pagination, Navigation, Autoplay} from 'swiper/modules';
import Image from "next/image";



export default function Service() {
    {/*  */}
    const prodcuts = [
        {
        title: "  Morita",
        desc: "  Morita UG (Automatic Fire Extinguisher System)",
        image: "/Service-1.webp"
        },
        {
        title: "  Miyata",
        desc: "-   Miyata (Automatic Fire Extinguisher System)",
        image: "/Service-2.webp"
        },
        {
        title: "  Statx",
        desc: "  Statx (Aerosol Fire suppression system)",
        image: "/Service-6.webp"
        },
    ]
    {/*  */}
    const services = [
        {
            title: "",
            desc: " ",
            image: "/Service-5.webp"
        },
        {
            title: "PM & Hydro Test Services",
            desc: " Preventive maintenance  Hydrostatic Test",
            image: "/p-8.webp"
        },
    ]
    {/*  */}
    const sampleImage = [
        "/p-1.webp",
        "/p-2.webp",
        "/p-3.webp",
        "/p-4.webp",
        "/p-5.webp",
        "/p-6.webp",
        "/p-7.webp",
        "/p-8.webp",
        "/p-9.webp",
        "/p-10.webp",
        "/p-11.webp",
        "/Service-1.webp",
        "/Service-2.webp",
        "/Service-3.webp",
        "/Service-4.webp",
        "/Service-5.webp"
    ]
    return (
        <section className="py-20 bg-gradient-to-b from-red-600 to-red-800 text-white">
            {/*  */}
            <div className="text-center mb-12 px-4">
                <h1 className="text-4xl sm:text-5xl font-bold mb-4"></h1>
                <p className="text-lg text-white/90 max-w-2xl mx-auto">
                        
                     / 
                </p>
            </div>
            {/*  */}
            <div className="max-w-7xl mx-auto mb-16 px-6">
                <h2 className="text-2xl font-bold mb-6 text-center"></h2>
                {/* Logo 3 Brand */}
                            <div className="flex items-center gap-8 justify-center mb-8 sm:mr-0">
                              {/* Morita */}
                              <div className="bg-white px-4 py-2 rounded-md flex items-center justify-center w-50 h-14">
                                <Image
                                  src="/morita-brand.webp"
                                  alt="Morita Brand"
                                  width={160}
                                  height={40}
                                  className="object-contain max-h-full max-w-full"
                                />
                              </div>
                
                              {/* Miyata */}
                              <div className="bg-white px-4 py-2 rounded-md flex items-center justify-center w-50 h-14">
                                <Image
                                  src="/miyata.webp"
                                  alt="Miyata Brand"
                                  width={160}
                                  height={40}
                                  className="object-contain max-h-full max-w-full"
                                />
                              </div>
                
                              {/* Statx */}
                              <div className="bg-white px-4 py-2 rounded-md flex items-center justify-center w-50 h-14">
                                <Image
                                  src="/Statx.webp"
                                  alt="Statx Brand"
                                  width={160}
                                  height={40}
                                  className="object-contain max-h-full max-w-full"
                                />
                              </div>
                            </div>
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8">
                    {prodcuts.map((product, index) => (
                        <div
                            key={index}
                            className="bg-white text-red-700 rounded-2xl shadow-lg p-8 flex flex-col items-center hover:scale-105 transition-transform"
                        >
                            <Image
                                src={product.image}
                                alt={product.title}
                                width={250}
                                height={100}
                                className="object-contain mb-4"
                                loading="lazy"
                            />
                            <h3 className="text-lg font-bold mb-2">{product.title}</h3>
                            <p className="text-base text-gray-700 text-center mb-4">{product.desc}</p>
                            <a href="#contact">
                                <button className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 cursor-pointer">
                                    
                                </button>
                            </a>
                        </div>
                    ))}
                </div>
            </div>

            {/*   */}
            <div className="max-w-5xl mx-auto px-6">
                <h2 className="text-2xl font-bold mb-6 text-center"></h2>
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-8">
                    {services.map((service, index) => (
                        <div
                            key={index}
                            className="bg-white text-red-700 rounded-2xl shadow-lg p-6 flex flex-col items-center hover:scale-105 transition-transform"
                        >
                            <Image
                                src={service.image}
                                alt={service.title}
                                width={160}
                                height={160}
                                className="object-contain mb-4"
                                loading="lazy"
                            />
                            <h3 className="text-xl font-bold mt-4 mb-2">{service.title}</h3>
                            <p className="text-sm text-gray-700 text-center mb-4">{service.desc}</p>
                            <a href="#contact">
                                <button className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 cursor-pointer">
                                    
                                </button>
                            </a>
                        </div>
                    ))}
                </div>
            </div>

            {/* Slide  */}
            <div className="max-w-7xl mx-auto mt-16 px-6">
                <h2 className="text-2xl font-bold mb-6 text-center"></h2>
                <Swiper
                    slidesPerView={1}
                    spaceBetween={20}
                    pagination={{ clickable: true}}
                    navigation
                    autoplay={{
                        delay: 2500,
                        disableOnInteraction: false,
                    }}
                    breakpoints={{
                        640: { slidesPerView: 2},
                        1024: { slidesPerView: 3}
                    }}
                    modules={[Pagination, Navigation, Autoplay]}
                    className="mySwiper"
                >
                    {sampleImage.map((img, idx) => (
                        <SwiperSlide key={idx} className="bg-white rounded-2xl shadow-lg p-4 flex items-center justify-center">
                            <img src={img} alt={`Sample ${idx}`} className="w-full h-48 object-contain" />
                        </SwiperSlide>
                    ))}
                </Swiper>
            </div>

            {/* CTA */}
            <div className="text-center mt-16">
                <h2 className="text-2xl font-bold mb-4">?</h2>
                <p className="text-white/90 mb-6"></p>
                <a
                    href="#contact"
                    className="px-6 py-3 bg-white text-red-700 font-semibold rounded-lg shadow hover:bg-gray-200"
                >
                    
                </a>
            </div>
        </section>
    )
}